import { getDatabase, ref, set, get } from "firebase/database";
import * as React from 'react';
import { useState } from "react";
import Engagement from './../imgs/logos/engagement.jpg';
import './Wedding.css';

const Wedding = () => {
    const [aname, setName] = useState('');
    const [guestNum, setGuestNum] = React.useState();

    // React.useEffect(() => {
    //     const db = getDatabase();
    //     const name = "Jamie Sps";

    //     get(ref(db, 'Invited/' + name)).then((snapshot) => {
    //         if (snapshot.val() !== null) { 
    //             set(ref(db, 'Confirmed/' + name), {
    //                 Name: name,
    //                 guestNum: 6
    //             });
    //         }
    //     });
    // });


    const handleNameChange = (name) => {
        setName(name.target.value);
    };

    const handleGuestChange = (guest) => {
        setGuestNum(guest.target.value);
    };

    return (
        <>
            <img className='engagement' src={Engagement} />
            <form className='form'>
                <br />
                <label>
                    <input type="text" value={aname} onChange={handleNameChange} placeholder='First and Last Name'/>
                </label>
                <br />
                <label>
                    <input type="number" value={guestNum} onChange={handleGuestChange} placeholder='Guests' />
                </label>
                <br />
                <input type="submit" value="Submit" />
            </form>
            <br />
        </>
    );
};

export default Wedding;
